/**
 * head
 * @param cssText 
 */
export function addCustomCSS(cssText) {
    let style = document.createElement('style') as any;  //style
    let head = document.head || document.getElementsByTagName('head')[0]; //head
    style.type = 'text/css'; //styletypetext/cssie
    if (style.styleSheet) { //IE
        let func = function () {
            try { //IEstylesheet
                style.styleSheet.cssText = cssText;
            } catch (e) {

            }
        }
        //styleSheet
        if (style.styleSheet.disabled) {
            setTimeout(func, 10);
        } else {
            func();
        }
    } else { //w3c
        //w3cstyle
        let textNode = document.createTextNode(cssText);
        style.appendChild(textNode);
    }
    head.appendChild(style); //stylehead    
}