import { BindingObject, Entity } from "@farris/devkit";
import { canJSONParse } from "../utils/ObjectUtils";

export class EntityParamUtils {
  /**
   * sourceVoEntityDataIdssourceVoEntityDatas
   * @param entities ID
   * @returns
   */
  static buildSourceVoEntityData(entities: Entity[]) {
    let sourceVoEntityDataIds: string[] = null;
    let sourceVoEntityDatas: string[] = null;
    // entitiesID
    // JSON.parse
    if (!entities.some((it: any) => ((typeof it) != "string") || (canJSONParse(it)))) {
      sourceVoEntityDataIds = entities as any[];
      sourceVoEntityDatas = null;
    } else {
      sourceVoEntityDataIds = null;
      sourceVoEntityDatas = entities.map(item => {
        if (item instanceof Entity) {
          // Entity
          return JSON.stringify((item as any).toJSON(false, true));
        } else if (item["__type__"] == "BindingObject") {
          //BindingData
          const bindingObject = item as BindingObject;
          return JSON.stringify(bindingObject.toJSON({ useFullMultiLangProperty: true }));
        } else if ((item as Entity).toJSON != null) {
          const e = item as Entity;
          return JSON.stringify((e as any).toJSON(false, true));
        } else {
          const e = item;
          return JSON.stringify(e);
        }
      });
    }

    return {
      sourceVoEntityDataIds: sourceVoEntityDataIds,
      sourceVoEntityDatas: sourceVoEntityDatas,
    }
  }
}
