export interface BrowserType {
    type?: "Opera" | "Safari" | "Chrome" | "IE" | "FF" | "Edge" | "Unknown";
    IEVersion?: "IE7" | "IE8" | "IE9" | "IE10" | "IE11" | "Unknown";

}

export function judgeBrowserType(): BrowserType {
    const browser: BrowserType = {
        type: "Unknown",
        IEVersion: undefined,
    };

    let userAgent = navigator.userAgent; //userAgent 
    let isOpera = userAgent.indexOf("Opera") > -1; //Opera 

    let isIE = userAgent.indexOf("compatible") > -1 && userAgent.indexOf("MSIE") > -1 && !isOpera; //IE 
    isIE = isIE || (!!window['ActiveXObject'] || "ActiveXObject" in window); //IE11IE

    let isEdge = userAgent.indexOf("Windows NT 6.1; Trident/7.0;") > -1 && !isIE; //IEEdge 
    let isFF = userAgent.indexOf("Firefox") > -1; //Firefox 
    let isSafari = userAgent.indexOf("Safari") > -1 && userAgent.indexOf("Chrome") == -1; //Safari 
    let isChrome = userAgent.indexOf("Chrome") > -1 && userAgent.indexOf("Safari") > -1; //Chrome 

    if (isIE) {
        browser.type = "IE";
        if (navigator.userAgent != null && !!navigator.userAgent.match(/Trident\/7\./)) {
            browser.IEVersion = "IE11";
        } else {
            let reIE = new RegExp("MSIE (\\d+\\.\\d+);");
            reIE.test(userAgent);
            let fIEVersion = parseFloat(RegExp["$1"]);
            if (fIEVersion == 7) {
                browser.IEVersion = "IE7";
            } else if (fIEVersion == 8) {
                browser.IEVersion = "IE8";
            } else if (fIEVersion == 9) {
                browser.IEVersion = "IE9";
            } else if (fIEVersion == 10) {
                browser.IEVersion = "IE10";
            } else if (fIEVersion == 11) {
                browser.IEVersion = "IE11";
            } else {
                //IE 
                browser.IEVersion = "Unknown";
            }
        }
    } //isIE end 

    if (isFF) {
        browser.type = "FF";
    }
    if (isOpera) {
        browser.type = "Opera";
    }
    if (isSafari) {
        browser.type = "Safari";
    }
    if (isChrome) {
        browser.type = "Chrome";
    }
    if (isEdge) {
        browser.type = "Edge";
    }
    return browser;
}